 /* **************************
  * 		GENERAL
  * - set body
  * - set headers (h1, h2, etc)
  * - set text (p, a, .fa, etc.)
  *  set theme (light, dark, special)
  */
body, html {
    font-family: sans-serif;
 	font-size: 16px;
 	line-height: 1.5em;
	letter-spacing: 0.5px;
	word-spacing: 0.05em;
	font-weight: 300;
    color: $color-grey-10;
}


h1, h2, h3, h4, h5, h6 {
	font-weight: 600;
	margin: 0;
	padding: 0;
	letter-spacing: 2px;
}
h1 {	/* name in header bar */
	font-size: 20px;
	line-height: 1.25em;
	letter-spacing: 1px;
}
h2 {	/* page title, ex: Resume/CV, Teaching */
	display: inline-block;
	margin: 0;
	padding: 0;
	position: relative;
	font-size: 48px;
	line-height: 1.25em;
	color: $color-grey-12;

	&:after {
		position: absolute;
		content: '';
		width: 100%;
		height: 2px;
		left: 0;
		bottom: 0;
		background: $color-grey-12;
	}
}
h3 {	/* page section headers, ex: Statement, Work Experience */
	font-size: 48px;
	line-height: 1.5em;
    text-align: center;
}
    .cvSection h3 { /* page sub-section headers, ex: Fellowships, Assistantships */
        background: $color-purple;
        color: $color-white;
        padding: $paddingEdgeMobile;
        font-size: 30px;
    	line-height: 1.25em;
        text-align: left;
    }
h4 {	
    font-size: 30px;
    line-height: 1.25em;
}
    .cvSection h4 { /* in-line role headers */
        font-size: 25px;
    	line-height: 1.1em;
        background: $color-grey-2;
        color: $color-grey-10;
        padding: ($paddingEdgeMobile - 10) $paddingEdgeMobile;
    }
h5 {    
    font-size: 20px;
	line-height: 1.1em;
}

p {
	margin-bottom: 15px;
	line-height: 1.65em;
}
a {
	text-decoration: none;
	&:hover { text-decoration: underline; }
}
	.fa, .fas, .far, .fab {
		font-size: 24px;
	}
    section, .light {
		a { color: $color-red; }
		.fa, .fas, .far, .fab { color: $color-purple; }
	}
    .special, header {
        p, a, .fa, .fas, .far, .fab {
            color: $color-white
        }
    }
	.dark, footer {
		p { color: $color-white; }
		a { color: $color-red2-bad; }
		.fa, .fas, .far, .fab { color: $color-grey-3; }
	}
b {
	font-weight: 400;
}
i {
	font-style: italic;
}

ul.remove-list-styles, ol.remove-list-styles {
	list-style: none;
	margin: 0;
	padding: 0;
	
	li {
		margin: 0;
	}
}
@media (min-width: $mediaBreakMediumMin) {
	h1 {
		font-size: 24px;
		line-height: 1.2em;
	}
    h3 {
        text-align: left;
    }
}



/* *******************
 * 		LAYOUT
 * - set the header to static (navigation is separate, see below)
 * - set footer to bottom of page
 *	// TODO: do this
 * - if aside, fix to 150px on right
 * // TODO: this has never been tested on mobile, styles pulled from v1
 */

$headerHeightMobile: 65px;
	$headerHeightScreen: 85px;
$headerZIndex: 1;

body {
    background: $color-white;
    display: grid;
    grid-template-rows: $headerHeightMobile auto auto;
    grid-gap: 100px 0;
    
    header, article, footer {
        display: grid;
        grid-template-columns: $paddingEdgeMobile auto $paddingEdgeMobile;
        grid-column: 1 / 2;
        
        > section {
    		grid-column: 2 / 3;
            
            .wrapper { 
                width: 100%;
                position: relative;
                display: grid;
                grid-gap: $paddingGap 0;
            }
            
            &.wide {
                grid-column: 1 / 4;
                
                .wrapper { 
                    margin-left: $paddingEdgeMobile;
                    margin-right: $paddingEdgeMobile;
                    width: auto;
                }
            }
            &#cv.wide {
                .wrapper { 
                    margin-left: 0;
                    margin-right: 0;
                }
            }
    	}
    }
    header {
    	position: fixed;
    	z-index: $headerZIndex;
    	top: 0;
    	height: $headerHeightMobile;
        width: 100%;
        
        grid-row: 1 / 2;
    }
    article {
        grid-row: 2 / 3;
        grid-gap: $paddingGap 0;
        
        > section {
            .wrapper { 
                margin-top: $paddingGap;
                margin-bottom: $paddingGap;
            }
        }
    }
    footer {
        grid-row: 3 / 4;
        grid-row-gap: $padding;
        
        > section:nth-child(1) { grid-row: 1 / 2; }
        > section:nth-child(2) { grid-row: 2 / 3; }
    }
}
@media (min-width: $mediaBreakMediumMin) {
	body {
        grid-template-rows: $headerHeightScreen auto auto;
        grid-gap: $paddingGap 0;
        
        header, article, footer {
            grid-template-columns: $paddingEdgeScreen auto $paddingEdgeScreen;
            
            > section.wide {
                .wrapper { 
                    margin-left: $paddingEdgeScreen;
                    margin-right: $paddingEdgeScreen;
                    grid-gap: $paddingGap 0;
                }
        	}
        }
        header {
    		height: $headerHeightScreen;
    	}
    }
}



/* ******************
 *		HEADER
 * - header bar
 *   - title
 *   - navigation
 */

header {
	background: linear-gradient(45deg, $color-purple0, $color-red0);

	-webkit-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
	-moz-box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
	box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);

    .wrapper {
		height: 100%;
        display: flex;
		flex-direction: row;
		justify-content: space-between;
        position: relative;

        #headerTitle {
            padding: 10px 0;
		    display: flex;
		    flex-direction: column;
		    justify-content: space-between;
            
			p {
				margin: 0;
				line-height: 1.25em;
			}
		}
        
        nav {
            position: absolute;
            z-index: ($headerZIndex + 1);
            right: 0;
            width: 100%;
            margin: 0 -5px;
            
    			&.close {
                    #navOpen { display: block; }
                    #navClose { display: none; }
                    #navLinks { display: none; }
    			}
    			&.open {
                    #navClose { display: block; }
                    #navLinks { display: block; }
                    #navOpen { display: none; }
    			}

            #navButton {
                height: $headerHeightMobile;
                width: $headerHeightMobile - 20;
                z-index: ($headerZIndex + 2);
                overflow: hidden;
                position: absolute;
                top: 0;
                right: 0;
                
                button {
                    height: $headerHeightMobile - 10;
                    width: $headerHeightMobile - 20;
                    padding: 0 0 10px 0;
                    margin: 10px 0 0 0;
                    position: absolute;
                    z-index: ($headerZIndex + 4);
                    border-radius: 50% 50% 0 0;
                    border: 0;
                    background: transparent;

                    .fa, .fas, .fab {
                        font-size: 30px;
                    }
                    &#navClose {
                        background: rgba(240, 240, 240, 0.17);
                        border: 1px solid rgba(109, 86, 184, 0.25);
                        border-bottom: 0;
                    }
                }
            }

            #navLinks {
                position: relative;
                top: $headerHeightMobile;
                border: 1px solid rgba(109, 86, 184, 0.5);
                border-top: 0;

                ul {
                    display: flex;
                    flex-direction: column;
                    list-style-type: none;
                    padding: 0;
                    text-align: center;
                    
                    li {
                        display: flex;
                        flex: 1;
                        align-items: flex-end;
        				justify-content: center;
        				border-bottom: 1px solid rgba(109, 86, 184, 0.5);
                        &:last-child { border: 0; }
                        
        				background: rgba(241, 238, 245, 0.98);
                        &.active {
                            background: linear-gradient(180deg, rgba(240, 240, 240, 0.98), rgba(109, 86, 184, 0.98));
                        }
                        a {
                            width: 100%;
                            display: block;
                            min-height: 70px;
                            line-height: 70px;
                            font-size: 20px;
                            color: $color-grey-12;
        					font-weight: 600;
                        }
                    }
                }
            }
        }
        
    }
}
@media (min-width: $mediaBreakMediumMin) {
	header {
        
        .wrapper {
    		
            #headerTitle {
                padding: 15px 0;
            }
            nav {
    			width: auto;
                margin: 0;
                
                &.close, &.open {
                    #navOpen { display: none; }
                    #navClose { display: none; }
                    #navLinks { display: block; }
    			}

    			#navButton { display: none; }
    			
                #navLinks {
    				width: auto;
    				padding: 0;
                    border: 0;
                    top: 0;

    				ul {
    					flex-direction: row;
    					border: 0;
    					li {
    						border: 0;
    						background: none;
                            margin-left: 5px;
                                
    						&.active {
    		                    background: linear-gradient(0deg, transparent, rgba(50, 50, 50, 0.2));
    		                }
    						a {
    							min-height: auto;
    							line-height: 20px;
    							font-size: 16px;
    							color: white;
    							padding: 50px 5px 20px;
    							font-weight: 400;
    						}
    					}
    				}
    			}
    		}
    	}
    }
}



/* ****************
 *		FOOTER
 */
footer {
    grid-template-rows: 100px 35px;
    
	background: $color-grey-10;
    background: linear-gradient(180deg, $color-grey-10, $color-grey-12);
    
	text-align: center;

	-webkit-box-shadow: 0 -3px 5px rgba(57, 63, 72, 0.3);
	-moz-box-shadow: 0 -3px 5px rgba(57, 63, 72, 0.3);
	box-shadow: 0 -3px 5px rgba(57, 63, 72, 0.3);
    
    p {
        font-size: 12px;
    }

	ul {
		list-style-type: none;
		padding: 0;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		text-align: center;
		li {
			a {
				display: block;
			}
		}
	}

	#sm {
		background: linear-gradient(45deg, $color-purple0, $color-red0);
		ul {
			li a {
				padding: $padding ($padding / 2);
				.fa, .fas, .fab {
					background: linear-gradient(135deg, transparent 20%, rgba(20,20,20,0.3));
					color: white;
					border-radius: 50%;
					padding: $padding;
					font-size: 20px;
					text-align: center;
					line-height: 20px;
				}
			}
		}
	}
}
@media (min-width: $mediaBreakMediumMin) {
	footer {
		.special {
			ul {
				li a {
					padding: 0 $padding;
					.fa, .fas, .fab {
						font-size: 20px;
						line-height: 20px;
					}
				}
			}
		}
        #sm {
            ul {
    			li a {
    				padding: $padding;
                }
            }
        }
	}
}



/* ******************
 *		CONTENT
 */
article {

	> section {
		&:nth-child(1) {
			background: $color-white;
		}
		&:nth-child(2) {
			background: $color-grey0;

			.inset {
				background: $color-grey-2;
				padding: $padding;
				margin: $padding -($padding) $padding $padding;
			}
		}
		&:nth-child(3) {
			background: $color-grey1;
		}
	}

	ol.numlist {
		list-style: none;
		counter-reset: numlist-counter;
		display: flex;
		flex-wrap: wrap;
		margin: 0;
		padding: 0;

		li {
			counter-increment: numlist-counter;
			margin: 25px 0;

			&::before {
				content: "0" counter(numlist-counter);
				font-family: monospace;
				background: linear-gradient(135deg, $color-grey0, $color-grey-1);
				color: $color-purple;
				border-radius: 50%;
				margin: 0 $padding 5px 0;
				display: inline-block;
				float: left;
				height: 100px;
				width: 100px;
				text-align: center;
				line-height: 100px;
				font-size: 45px;
				font-weight: 600;
			}

			b {
				display: block;
				margin-bottom: 5px;
				font-weight: 600;
			}
		}
	}

	#welcome {

		.blurb {
            background: $color-grey-1;
            padding: $padding;
            text-align: center;
            p {
                text-align: left;
            }
		}
	}

	ul.list {
		padding: 0;
		list-style-type: none;
	}
}
@media (min-width: $mediaBreakMediumMin) {

}

.cvSubSection {
    grid-column: 1 / 4;
}
.cvSection, .cvSubSection {
    display: grid;
    grid-gap: 20px 0;
    grid-template-columns: $paddingEdgeMobile auto $paddingEdgeMobile;
    
    h3, h4 {
        grid-column: 1 / 4;
        grid-row: 1 / 2;
    }
    .summary { grid-column: 2 / 3; }
    
    .cvPosition {
        display: grid;
        grid-template-columns: auto 110px;
        grid-column: 2 / 3;
        grid-gap: 10px 0;
        
        .role, .org, .date, .details {
    		p {
    			margin-bottom: 0;
                line-height: 1.45em;
    		}
    	}
        .role {
            grid-column: 1 / 2;
        }
    	.date { 
            text-align: right;
            grid-column: 2 / 3;
        }
        .org {
            font-style: italic;
            grid-column: 1 / 3;
        }
        .details {
            grid-column: 1 / 3;
        }
    }
}
.pageSection {
    display: grid;
    grid-gap: $paddingGap 0;
    grid-template-columns: $paddingEdgeMobile auto $paddingEdgeMobile;
    
    .pagePosition { 
        grid-column: 2 / 3;
        display: flex;
        flex-direction: column;
        margin: 0;
        
        .image {
            text-align: center;
            flex: 1 0;
            margin: 0 0 10px 0;
            img {
                width: 50%;
            }
        }
    	.details {
            p { margin-bottom: 0; }
            .org {
                font-style: italic;
            }
    		.materials {
    			display: flex;
    			align-items: flex-start;
    			justify-content: space-between;
    			&.left {
    				flex-direction: row;
    				> div { flex-direction: row; }
    			}
    			&.right {
    				flex-direction: row-reverse;
    				> div { flex-direction: row-reverse; }
    			}
    		}
    	}
    	.classes {
    		> ul > li {
    			margin-top: 20px;
    			&:first-child { margin-top: 0; }
    		}
    		.works h4 { font-size: 1.4em; }
    	}
    }
}
.works {
    .work {
        .workRef {
            text-indent: -20px;
            margin: 5px 0 0 20px;
            line-height: 1.25;
        }
    }
}
@media (min-width: $mediaBreakMediumMin) {
    .cvSection, .cvSubSection {
        .cvPosition {
            grid-template-columns: 200px auto;
            grid-gap: 5px $paddingGap;
            
            .date { 
                grid-column: 1 / 2;
                grid-row: 1 / 4;
            }
            .role {
                grid-column: 2 / 3;
            }
            .org, .details {
                grid-column: 2 / 3;
            }
        }
    }
    .pageSection {
        grid-template-columns: auto;
        .pagePosition { 
            grid-column: 1 / 2;
            flex-direction: row;
            .image { 
                flex: 1 0 200px;
                margin: 0 50px0 0;
                img {
                    width: 100%;
                }
            }
        }
    }
}

#about {
	display: flex;
	flex-direction: column;
	
	h1 {
		font-size: 3em;
		color: $color-purple0;
		span { color: $color-grey-9; }
	}
	h2 {
		font-size: 1.5em;
    	margin-bottom: $paddingGap;
		color: $color-grey-8;
		&:after { display: none; }
	}
	.description {
		
	}
	.headshot {
        display: none;
    }
}
@media (min-width: $mediaBreakMediumMin) {
    #about {
        flex-direction: row;
        
        h1 {
    		font-size: 4em;
        }
            
        .headshot {
            display: block;
    		margin-left: 20px;
            flex-basis: auto;
            min-width: auto;
    		img {
    			border-radius: 50%;
    			border: 6px solid $color-grey0;
    			padding: 2px;
    		}
    	}
    }
}


/* Show Hide Group */
.sh-group {
	&.sh-close .sh-details {
		opacity: 0;
		height: 0;
	}
	&.sh-open .sh-details {
		opacity: 1;
		height: auto;
	}
	.sh-details {
		transition: all 300ms ease-out;
	}
	
}

#timeline { display: none; }
@media (min-width: $mediaBreakMediumMin) {

	#timeline {
		display: block;
		margin: 20px -10% 50px;
		text-align: center;
        display: none;

		#timeline-grid {

			.timeline-group, .timeline-header {
				display: flex;
				flex-direction: row;
				align-items: center;

				.timeline-label {
					width: 130px;
					display: flex;
					justify-content: center;
					align-items: center;

					h4 {
						font-size: 16px;
						letter-spacing: 1px;
						margin: 0;
					}
				}
				.timeline-graphs {
					flex-grow: 999;
					position: relative;
				}
				.timeline-graph {
					flex-grow: 999;
					position: relative;

					span {
						position: absolute;
						left: 0;
						top: 0;
						overflow: hidden;
						height: 44px;
						width: 0;
						padding: 10px 0;
						margin: 5px 0.25%;
						display: flex;
						justify-content: center;
						align-items: center;
						font-size: 12px;
						line-height: 1.2em;
						animation: all 2 ease;
					}
				}
			}
				.timeline-header {
					.timeline-graph {
						height: 30px;
						span {
							border-right: 1px dotted $color-grey-6;
							align-items: flex-start;
							padding: 0 1px 0 2px;
							margin: 0;
							height: 0;
							color: black;
							&:last-child { border: 0; }
						}
					}
				}
				.timeline-group {
					&:nth-child(odd) {
						background: transparent;
						.timeline-graph span {
							background: $color-purple4;
						}
					}
					&:nth-child(even) {
						background: $color-grey-1;
						.timeline-graph span {
							background: $color-purple2;
						}
					}
					.timeline-label {
						padding: 10px;
					}
					.timeline-graph {
						height: 55px;
						span {
							color: white;
							text-shadow: 0 0 5px rgba(100,100,100, 1);
						}
					}
				}

		}
	}
}
